* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}

.Header {
    height: 75px;
    width: 100%;

    .container {
        display: flex;
        align-items: center;
        justify-content: space-between;
        width: 90%;
        height: 75px;
        margin-left: auto;
        margin-right: auto;
    }

    &-logo {
        width: 97px;
        height: 31px;
        margin-top: 15px;
        margin-left: 15px;
    }

    &-sehir {
        font-family: "Inter";
    }
}