.Section {
    width: 100%;
    height: 600px;
    margin-top: 20px;

    .container {
        width: 90%;
        height: 600px;
        margin-left: auto;
        margin-right: auto;
    }


    &-title {
        text-align: center;
        font-family: "Inter";
        margin-bottom: 15px;
    }

    &-card {

        &-bir {
            height: 100px;
            width: 100%;
            display: flex;
            margin-bottom: 15px;

            &-foto {
                height: 100px;
                width: 100px;
                background-color: #295135;
                border-radius: 5px;
            }

            &-yazi {
                height: 100px;
                width: 183px;
                background-color: #80BC00;
                margin-left: 5px;
                border-radius: 5px;
            }
        }

        &-iki {
            height: 100px;
            width: 100%;
            display: flex;
            margin-bottom: 15px;

            &-foto {
                height: 100px;
                width: 100px;
                background-color: #295135;
                border-radius: 5px;

            }

            &-yazi {
                height: 100px;
                width: 183px;
                background-color: #80BC00;
                margin-right: 5px;
                border-radius: 5px;
            }
        }

        &-üç {
            height: 100px;
            width: 100%;
            display: flex;

            &-foto {
                height: 100px;
                width: 100px;
                background-color: #295135;
                border-radius: 5px;
            }

            &-yazi {
                height: 100px;
                width: 183px;
                background-color: #80BC00;
                margin-left: 5px;
                border-radius: 5px;
            }
        }
    }

    .Highlights-slider {
        margin-top: 50px;
        height: 100px;
        width: 100%;

        .Highlights-item {
            height: 100px;
            width: 33%;
            background-color: #295135;
            border-radius: 10px;
            margin: 0 5px;
        }

        .slider-foto {
            height: 100%;
            width: 100%;
            border-radius: 10px;
            padding: 2px;
        }
    }
}

.card-foto {
    height: 100px;
    width: 100px;
    border-radius: 5px;
    padding: 2px;
}

.text {
    font-size: 0.7em;
    padding: 5px;
}